// Redux Toolkit permet d'crire un code moins "boilerplate" (-rptitif).
import { createSlice } from "@reduxjs/toolkit";
import { loginUser } from "../../redux/thunks/loginUser";
import { getUserProfile } from "../../redux/thunks/getUserProfile";
import { updateUserName } from "../../redux/thunks/updateUserName";
/*
  createSlice (redux toolkit) permet de dfinir plus simplement(+concis,-verbeux) les reducers et les actions correspondantes.
  immer (redux toolkit) sous le capot permet de traiter les mises  jour du state de manire immuable (code +lisible +sr) et
  vite aussi les destructurations en profondeur (plusieurs niveaux de dstructuration).
  immuable = une fois un objet cr il ne peut tre modifi.Pour le modifier il faut crer une copie et faire les modifs.
*/
const authSlice = createSlice({
  name: "auth",
  // dfinit l'tat avant toute action. Si l'tat est undefined alors l'tat initial sera renvoy(-erreurs).
  initialState: {
    user: {
      firstName: null,
      lastName: null,
      userName: null,
    },

    token: null,
    isAuthenticated: false,
    error: null,
  },
  //Actions
  reducers: {
    // reducers = si cette action se produit voici comment je change mon state.
    // action pour rinitialiser le state lorsque l'user se dconnecte.
    logout: (state) => {
      state.isAuthenticated = false;
      state.user = state.token = state.error = null;
    },
    clearError: (state) => {
      state.error = null;
    },
  },

  // Action pour mettre  jour le state lorsque l'user se connecte.
  // 'pending' pas implment car utilis pour indiquer une attente (isLoading)comme un spinner.
  extraReducers: (builder) => {
    // extraReducers = proprit qui coute les actions en dehors du slice (asynchrones ou autres externes )
    // modifie le state en fonction de la rponse de la requte (attente, russite, chec).
    builder // builder = outil pour chaner plusieurs actions et dfinir les rponses
      // russite
      .addCase(loginUser.fulfilled, (state, action) => {
        state.isAuthenticated = true;
        state.token = action.payload.body.token;
        state.error = null;
      })
      // chec
      .addCase(loginUser.rejected, (state, action) => {
        state.error = action.payload; // message d'erreur recu par thunkAPI.rejectWithValue(error.message)
      })
      .addCase(getUserProfile.fulfilled, (state, action) => {
        state.user = action.payload;
        state.error = null;
      })
      .addCase(getUserProfile.rejected, (state, action) => {
        state.error = action.payload;
      })
      .addCase(updateUserName.fulfilled, (state, action) => {
        state.user.userName = action.payload.body.userName; 
        state.error = null;
      })
      .addCase(updateUserName.rejected, (state, action) => {
        state.error = action.payload; 
      });
  },
});

export const { logout, clearError } = authSlice.actions;
export default authSlice.reducer;
